import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material';
import { DialogData } from '../dialog-lead/dialog-lead.component';
import { DialogCompraComponent } from '../dialog-compra/dialog-compra.component';
import { AngularFirestore } from '@angular/fire/firestore';
import { Router } from '@angular/router';

@Component({
  selector: 'app-resultado-quiz',
  templateUrl: './resultado-quiz.component.html',
  styleUrls: ['./resultado-quiz.component.less']
})
export class ResultadoQuizComponent {

  constructor(
    public dialogRef: MatDialogRef<ResultadoQuizComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private dialog: MatDialog,
    private db: AngularFirestore,
    private router: Router) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

  queroComprar(planetaId) {
    this.onNoClick();
    const dialogRef = this.dialog.open(DialogCompraComponent, {
      data: {
        nome: '',
        email: '',
        incrementeSuaViagem: {
          esquiar: false,
          montanhasRochosas: false,
          noiteRomantica: false,
          mundosGelados: false
        },
        data: null
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!result) {
        this.router.navigate(['/']);
        return false;
      }
      this.router.navigate(['/']);
      this.db.collection('compras').add({ ...result, planetaId, dataInclusao: new Date() })
    });
  }

}
