import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-relatorio',
  templateUrl: './relatorio.component.html',
  styleUrls: ['./relatorio.component.less']
})
export class RelatorioComponent implements OnInit {
  compras: number;
  leads: number;
  quiz: number;
  constructor(private db: AngularFirestore) { }

  ngOnInit() {
    this.db.collection('compras').valueChanges().subscribe((compras: any) => {
      this.compras = compras;
    })
    this.db.collection('leads').valueChanges().subscribe((leads: any) => {
      this.leads = leads;
    })
    this.db.collection('quiz').valueChanges().subscribe((quiz: any) => {
      this.quiz = quiz;
    })
  }

}
