import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { AngularFirestore } from '@angular/fire/firestore';
import { MatDialog } from '@angular/material';
import { ResultadoQuizComponent } from '../resultado-quiz/resultado-quiz.component';

@Component({
  selector: 'app-perfil-planetario',
  templateUrl: './perfil-planetario.component.html',
  styleUrls: ['./perfil-planetario.component.less']
})
export class PerfilPlanetarioComponent implements OnInit {

  quiz: FormGroup;

  constructor(private formBuilder: FormBuilder, private db: AngularFirestore, private dialog: MatDialog) {
    this.startForm();
  }

  ngOnInit() {
  }

  enviarQuiz() {
    if (!this.quiz.value || this.quiz.invalid) return;
    this.db.collection('quiz').add({ ...this.quiz.value, dataInclusao: new Date() });
    this.analisaQuiz(this.quiz.value);
    this.startForm();
  }

  startForm() {
    this.quiz = this.formBuilder.group({
      email: new FormControl('', Validators.required),
      voceSeConsidera: new FormControl('', Validators.required),
      idade: new FormControl(null, Validators.required),
      ondeVive: new FormControl(null, Validators.required),
      profissao: new FormControl(null, Validators.required),
      filhos: new FormControl(false),
      pretendeViajarComOsFilhos: new FormControl(false),
      estadoCivil: new FormControl(false, Validators.required),
      tempoLivre: new FormControl('', Validators.required),
      esporteVoceGosta: new FormControl('', Validators.required),
      ficcaoCientifica: new FormControl(false),
      destinoDosSonhos: new FormControl('', Validators.required),
      viagemComum: new FormControl('', Validators.required),
      sonhoDeSerAstronauta: new FormControl(false),
    });
  }

  analisaQuiz(quiz) {
    let perfilVoe = 0;
    let perfilDescubra = 0;
    let perfilAventure = 0;
    let perfilConecte = 0;

    if (quiz.tempoLivre === 'artesanato') {
      perfilVoe++;
    } else if (quiz.tempoLivre === 'arlivre') {
      perfilDescubra++;
    } else if (quiz.tempoLivre === 'cozinhar') {
      perfilAventure++;
    } else if (quiz.tempoLivre === 'filmeSeries') {
      perfilConecte++;
    }

    if (quiz.esporteVoceGosta === 'tenis') {
      perfilVoe++;
    } else if (quiz.esporteVoceGosta === 'luta') {
      perfilDescubra++;
    } else if (quiz.esporteVoceGosta === 'volei') {
      perfilAventure++;
    } else if (quiz.esporteVoceGosta === 'futebol') {
      perfilConecte++;
    }

    if (quiz.viagemComum === 'relaxante') {
      perfilVoe++;
    } else if (quiz.viagemComum === 'aventura') {
      perfilDescubra++;
    } else if (quiz.viagemComum === 'espiritual') {
      perfilAventure++;
    } else if (quiz.viagemComum === 'historica') {
      perfilConecte++;
    }

    let planeta;

    if (perfilVoe) { planeta = 1 }
    else if (perfilDescubra) { planeta = 2 }
    else if (perfilAventure) { planeta = 3 }
    else if (perfilConecte) { planeta = 4 }
    else { planeta = Math.floor(Math.random() * (5 - 1) + 1) }
    this.dialog.open(ResultadoQuizComponent, {
      data: {
        planeta
      }
    });
  }
}
